  display: flex;
  text-align: center;
  -ms-flex: 1 1 100%;
      flex: 1 1 100%;
  height: 100%;
  position: absolute;
  width: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App-header {
  background-color: #222;
